___CSS_LOADER_EXPORT___.push([module.id, `* {
	margin: 0;
	padding: 0;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;

	-webkit-transition: background-color 0.3s ease, opacity 0.3s ease;

	-o-transition: background-color 0.3s ease, opacity 0.3s ease;

	transition: background-color 0.3s ease, opacity 0.3s ease;
	scroll-behavior: smooth;
}

html {
	font-size: 16px;
}
ul,
li {
	display: block;
	padding: 0;
	margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: inherit;
	font-weight: 700;
	color: var(--accent-black);
}

p {
	margin: 0;
}
img {
	display: block;
	width: 100%;
}

body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    background-color: black;
    color: white;
}


.header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
    background-color: #333;
    max-height: 170px;
}

.counts {
}
.score,
.miss {
    font-size: 24px;
    font-weight: 600;
}
.score {
    color: green;
}
.miss {
    color: red;
}

.main {
    height: calc(100vh - 170px);
    display: flex;
    align-items: center;
    justify-content: center;
}
.field {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin: auto;
    width: 600px;
    height: 600px;
    border: 5px solid white;
    gap: 15px;
}

.field-item {
    width: 20%;
    height: 20%;
    display: inline-block;
    border: 1px solid white;
    cursor: pointer;
}
.field-item img {
    height: 100%;
}

.btns {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-top: 20px;
}
.btn {
    cursor: pointer;
    padding: 10px 20px;
    border: none;
    color: white;
    text-transform: uppercase;
    font-weight: 600;
    background-color: white;
    border-radius: 15px;
}
.btn:hover {
    opacity: .7;
}
.btn-start {
    background-color: green;
}
.btn-stop {
    background-color: red;
}`, "",{"version":3,"sources":["webpack://./src/css/style.css"],"names":[],"mappings":"AAAA;CACC,SAAS;CACT,UAAU;CACV,8BAA8B;CAC9B,sBAAsB;;CAEtB,iEAAiE;;CAEjE,4DAA4D;;CAE5D,yDAAyD;CACzD,uBAAuB;AACxB;;AAEA;CACC,eAAe;AAChB;AACA;;CAEC,cAAc;CACd,UAAU;CACV,SAAS;AACV;;AAEA;;;;;;CAMC,oBAAoB;CACpB,gBAAgB;CAChB,0BAA0B;AAC3B;;AAEA;CACC,SAAS;AACV;AACA;CACC,cAAc;CACd,WAAW;AACZ;;AAEA;IACI,SAAS;IACT,UAAU;IACV,8BAA8B;IAC9B,uBAAuB;IACvB,YAAY;AAChB;;;AAGA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;IACb,sBAAsB;IACtB,iBAAiB;AACrB;;AAEA;AACA;AACA;;IAEI,eAAe;IACf,gBAAgB;AACpB;AACA;IACI,YAAY;AAChB;AACA;IACI,UAAU;AACd;;AAEA;IACI,2BAA2B;IAC3B,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;AACA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,eAAe;IACf,YAAY;IACZ,YAAY;IACZ,aAAa;IACb,uBAAuB;IACvB,SAAS;AACb;;AAEA;IACI,UAAU;IACV,WAAW;IACX,qBAAqB;IACrB,uBAAuB;IACvB,eAAe;AACnB;AACA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;IACT,gBAAgB;AACpB;AACA;IACI,eAAe;IACf,kBAAkB;IAClB,YAAY;IACZ,YAAY;IACZ,yBAAyB;IACzB,gBAAgB;IAChB,uBAAuB;IACvB,mBAAmB;AACvB;AACA;IACI,WAAW;AACf;AACA;IACI,uBAAuB;AAC3B;AACA;IACI,qBAAqB;AACzB","sourcesContent":["* {\n\tmargin: 0;\n\tpadding: 0;\n\t-webkit-box-sizing: border-box;\n\tbox-sizing: border-box;\n\n\t-webkit-transition: background-color 0.3s ease, opacity 0.3s ease;\n\n\t-o-transition: background-color 0.3s ease, opacity 0.3s ease;\n\n\ttransition: background-color 0.3s ease, opacity 0.3s ease;\n\tscroll-behavior: smooth;\n}\n\nhtml {\n\tfont-size: 16px;\n}\nul,\nli {\n\tdisplay: block;\n\tpadding: 0;\n\tmargin: 0;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n\tfont-family: inherit;\n\tfont-weight: 700;\n\tcolor: var(--accent-black);\n}\n\np {\n\tmargin: 0;\n}\nimg {\n\tdisplay: block;\n\twidth: 100%;\n}\n\nbody {\n    margin: 0;\n    padding: 0;\n    font-family: Arial, sans-serif;\n    background-color: black;\n    color: white;\n}\n\n\n.header {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    padding: 10px